/* Universal Section Styling */
section {
  position: relative;
  width: 100vw;
  box-sizing: border-box;
  padding: 60px 40px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #e0e5ec;
  color: #000;
  margin-bottom: 80px;
}

/* ABOUT SECTION */
.about-section {
  max-width: calc(100vw - 220px);
  margin-left: 220px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  min-height: 100vh;
  overflow: hidden;
  z-index: 0;
  margin-bottom: 2.5rem;
}

.about-section::before {
  content: none;
}

.about-content, .education-section, .certification-section {
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 4px 24px #e0e5ec, 0 1.5px 6px #bec4d0;
  padding: 2rem 1.5rem;
  margin: 1.5rem 0;
  width: 100%;
  max-width: 900px;
  transition: box-shadow 0.2s;
}

.about-content:hover, .education-section:hover, .certification-section:hover {
  box-shadow: 0 8px 32px #c1c6d4, 0 2px 8px #ffffff;
}

.section-title {
  font-size: 2.2rem;
  font-weight: 700;
  color: #1e90ff;
  border-bottom: 4px solid #1e90ff;
  padding-bottom: 10px;
  margin-bottom: 30px;
  letter-spacing: 0.01em;
}

.section-title .highlight {
  color: #f94144;
}

.section-subtitle {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 30px;
  font-style: italic;
}

.about-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 900px;
  gap: 30px;
}

.about-content > div {
  margin-bottom: 30px;
}

.about-text p {
  background-color: #e0e5ec;
  border-radius: 12px;
  padding: 20px 25px;
  font-size: 1.1rem;
  line-height: 1.7;
  color: #000;
  box-shadow: 8px 8px 16px #bec4d0, -8px -8px 16px #ffffff;
  text-align: left;
  width: 100%;
  box-sizing: border-box;
}

/* CERTIFICATION SECTION */
.certification-section,
.education-section {
  background-color: #e0e5ec;
  border-radius: 12px;
  padding: 25px 30px;
  width: 100%;
  max-width: 900px;
  box-sizing: border-box;
  box-shadow: 8px 8px 16px #bec4d0, -8px -8px 16px #ffffff;
  margin-left: 0;
  min-height: 200px;
  margin-top: 24px;
  overflow: hidden;
}

.certification-title,
.education-title {
  font-size: 1.4rem;
  color: #1e90ff;
  border-bottom: 2px solid #1e90ff;
  padding-bottom: 8px;
  margin-bottom: 18px;
  text-align: center;
}

.certification-list,
.education-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.certification-item,
.education-item {
  background: #f5f7fa;
  border-radius: 12px;
  padding: 1rem 1.2rem;
  box-shadow: 0 2px 8px #e0e5ec;
  text-align: left;
}

.certification-item h4,
.education-item h4 {
  font-size: 1.1rem;
  margin: 0 0 6px 0;
  color: #1e90ff;
}

.certification-item p,
.education-item p {
  font-size: 1rem;
  margin: 0;
  color: #333;
}

/* CONTACT SECTION */
.contact-info-section {
  min-height: 100vh;
  margin-left: 220px;
  padding: 40px 0;
  text-align: center;
  background-color: #e0e5ec;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  box-sizing: border-box;
  overflow-x: hidden;
  z-index: 0;
}

.contact-info-section::before {
  content: none;
}

.info-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  width: 100%;
  max-width: 1200px;
  margin-top: 20px;
}

.info-card {
  background-color: #e0e5ec;
  padding: 25px 20px;
  width: 260px;
  border-radius: 16px;
  box-shadow: 8px 8px 16px #bec4d0, -8px -8px 16px #ffffff;
  transition: all 0.3s ease;
  color: #000;
  text-align: center;
  cursor: pointer;
  margin-bottom: 20px;
  min-height: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.info-card:hover {
  transform: translateY(-6px);
  box-shadow: inset 4px 4px 10px #c1c6d4, inset -4px -4px 10px #ffffff;
}

.info-icon {
  width: 50px;
  margin-bottom: 15px;
  filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.15));
}

/* RESPONSIVE */
@media (max-width: 900px) {
  .info-cards {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .info-card {
    width: 90%;
    margin-bottom: 20px;
    min-height: 160px;
  }
}

@media (max-width: 768px) {
  section {
    padding: 24px 6px;
    margin-bottom: 40px;
    width: 100vw;
    min-width: 0;
    box-sizing: border-box;
  }

  .about-section {
    margin-left: 0;
    max-width: 100vw;
    padding: 1.2rem 0.5rem;
  }

  .about-content, .education-section, .certification-section {
    padding: 1rem 0.5rem;
    border-radius: 12px;
    max-width: 98vw;
  }

  .about-content,
  .certification-list,
  .info-cards {
    flex-direction: column;
    align-items: center;
    gap: 16px;
    width: 100%;
    max-width: 100vw;
  }

  .about-text p,
  .education-section,
  .certification-section,
  .info-card {
    width: 98vw;
    min-width: 0;
    box-sizing: border-box;
    padding-left: 8px;
    padding-right: 8px;
  }

  .info-icon {
    width: 40px;
  }

  .section-title {
    font-size: 1.5rem;
    padding-bottom: 4px;
    margin-bottom: 16px;
  }

  .certification-title,
  .education-title {
    font-size: 1.2rem;
    padding-bottom: 4px;
    margin-bottom: 10px;
  }
}
