/* Profile Image Styling */
.profile-pic {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 8px 8px 16px #cfd8e1, -8px -8px 16px #ffffff;
  margin-bottom: 20px;
  animation: fadeInContent 1s ease-in-out;
}

/* Resume button with subtle neumorphism */
.resume-button {
  padding: 0.75rem 1.5rem;
  background-color: #f94144;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 12px;
  font-size: 1rem;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
  display: inline-block;
  margin-top: 2rem;
  box-shadow: 6px 6px 10px #c03030, -6px -6px 10px #ff6f6f;
}

.resume-button:hover {
  background-color: #c53030;
  box-shadow: inset 4px 4px 6px #a22a2a, inset -4px -4px 6px #dd4848;
}

/* Fade-in animation for home content */
@keyframes fadeInContent {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.home-content {
  padding: 60px 40px;
  background-color: #e0e5ec;
  text-align: center;
  animation: fadeInContent 1s ease forwards;
  color: #000;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* New card style for home/profile section */
.home-content.card {
  background: #fff;
  border-radius: 24px;
  box-shadow: 0 4px 24px #e0e5ec, 0 1.5px 6px #bec4d0;
  padding: 2.5rem 2rem 2rem 2rem;
  margin: 2rem auto 2.5rem auto;
  max-width: 600px;
  text-align: center;
  transition: box-shadow 0.2s;
}

.home-content.card:hover {
  box-shadow: 0 8px 32px #c1c6d4, 0 2px 8px #ffffff;
}

/* Slight text shadow for better contrast */
.home-content h1 {
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.6);
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.home-content p {
  text-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
  font-size: 1.1rem;
  margin-bottom: 10px;
}

/* Skills Section */
.skills-section {
  margin-top: 40px;
  margin-bottom: 1.5rem;
}

.skills-section h2 {
  font-size: 1.8rem;
  color: #1e90ff;
  margin-bottom: 20px;
  font-weight: 700;
}

.skills-list {
  list-style-type: none;
  padding: 0;
  font-size: 1rem;
  text-align: left;
  max-width: 600px;
  margin: 0 auto;
}

.skills-list li {
  margin-bottom: 10px;
  margin-bottom: 0.7rem;
  font-size: 1.08rem;
  padding-left: 1.2em;
  position: relative;
}

.skills-list li:before {
  content: 'â€¢';
  color: #1e90ff;
  position: absolute;
  left: 0;
}

/* Responsive */
@media (max-width: 768px) {
  .profile-pic {
    width: 140px;
    height: 140px;
  }

  .home-content {
    padding: 40px 20px;
  }

  .home-content.card {
    padding: 1.2rem 0.5rem 1.5rem 0.5rem;
    border-radius: 14px;
    max-width: 98vw;
  }

  .home-content h1 {
    font-size: 2rem;
  }

  .skills-list {
    font-size: 0.95rem;
  }

  .resume-button {
    font-size: 0.95rem;
    padding: 0.6rem 1.2rem;
  }

  .profile-pic {
    width: 90px;
    height: 90px;
  }
}
