/* Reset & Base */
body, html, #root {
  margin: 0;
  height: 100%;
  font-family: 'Inter', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #e0e5ec 0%, #f5f7fa 100%);
  color: #222;
  letter-spacing: 0.01em;
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  background: none;
}

/* Sticky Navigation Bar */
.sticky-nav {
  position: sticky;
  top: 0;
  z-index: 100;
  background: rgba(255,255,255,0.95);
  box-shadow: 0 2px 8px rgba(30,144,255,0.08);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0;
  gap: 2rem;
}
.sticky-nav a {
  color: #1e90ff;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.1rem;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  transition: background 0.2s, color 0.2s;
}
.sticky-nav a:hover, .sticky-nav a.active {
  background: #1e90ff;
  color: #fff;
}

/* Sidebar Styling */
.sidebar {
  background: #e0e5ec;
  width: 280px;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-shadow: inset 8px 8px 16px #bec4d0,
              inset -8px -8px 16px #ffffff;
  border-radius: 20px;
  margin: 20px;
}

.sidebar h2 {
  margin-bottom: 30px;
  font-weight: 700;
  color: #1e90ff;
}

.sidebar nav {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.sidebar nav a {
  color: #000;  /* BLACK TEXT */
  text-decoration: none;
  padding: 12px 20px;
  margin-bottom: 15px;
  font-size: 18px;
  border-radius: 12px;
  box-shadow: 6px 6px 12px #bec4d0,
              -6px -6px 12px #ffffff;
  transition: all 0.2s ease;
}

.sidebar nav a.active,
.sidebar nav a:hover {
  background-color: #1e90ff;
  color: white;
  box-shadow: inset 4px 4px 8px #186cd3,
              inset -4px -4px 8px #249cff;
}

/* Main content area */
.main-content {
  flex-grow: 1;
  min-width: 0;
  padding: 30px 0;
  overflow-y: auto;
}

/* Reusable card style */
.card {
  background: #fff;
  border-radius: 20px;
  padding: 32px 28px;
  box-shadow: 0 4px 24px #e0e5ec, 0 1.5px 6px #bec4d0;
  max-width: 1000px;
  margin: 2rem auto;
  color: #222;
  transition: box-shadow 0.2s;
}
.card:hover {
  box-shadow: 0 8px 32px #c1c6d4, 0 2px 8px #ffffff;
}

.card h1, .card h2, .card h3 {
  font-family: 'Inter', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 700;
  color: #1e90ff;
  margin-bottom: 1rem;
}

.card p, .card ul, .card label {
  font-size: 1.1rem;
  line-height: 1.7;
  color: #222;
}

/* Form Styling */
.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 15px;
  font-size: 16px;
  margin-bottom: 20px;
  border: none;
  border-radius: 12px;
  background: #e0e5ec;
  box-shadow: inset 6px 6px 12px #bec4d0,
              inset -6px -6px 12px #ffffff;
  resize: vertical;
  outline: none;
  color: #000;  /* BLACK TEXT */
}

.contact-form button {
  background-color: #1e90ff;
  color: white;
  padding: 12px 30px;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 4px 4px 10px #186cd3,
              -4px -4px 10px #249cff;
  transition: background 0.3s ease;
}

.contact-form button:hover {
  background-color: #0f6cd9;
}

/* Resume Button */
.resume-button {
  background: linear-gradient(90deg, #1e90ff 60%, #249cff 100%);
  color: #fff;
  padding: 14px 36px;
  border: none;
  border-radius: 24px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 2px 8px #1e90ff33;
  margin: 1.5rem 0 2rem 0;
  transition: background 0.2s, box-shadow 0.2s;
}
.resume-button:hover {
  background: #0f6cd9;
  box-shadow: 0 4px 16px #1e90ff44;
}

/* All-in-One Container */
.all-in-one-container {
  display: flex;
  flex-direction: column;
  width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .app-container {
    flex-direction: column;
  }

  .sidebar {
    width: 100%;
    margin: 10px;
    border-radius: 12px;
    align-items: center;
  }

  .main-content {
    padding: 10px 0;
  }

  .card {
    padding: 18px 8px;
    border-radius: 12px;
  }

  .sticky-nav {
    gap: 0.5rem;
    font-size: 0.95rem;
  }
}
